body { 
  margin:0 auto;
  width:80%;
  max-width:1100px;
  font-family: 'Lora', serif;
  font-weight:400;
}

a { 
  color:black;
  text-decoration-color:rgb(229, 255, 0);
}

p {
  line-height:1.65em;
  margin-bottom:.55em;
  font-size: 1.25em;
}

h1,h2 {  font-weight:200; margin:0;
  }

h3 {  font-weight:300; margin:0;
  }

input {
  margin-right:50px;
}
header {
 position:relative;
}

header h1 {
  font-family: 'Lora', serif;
  font-size: 4em;
  margin-left: -5px;
  line-height:1em;
  letter-spacing: -.02em;
  word-spacing: -.04em;
  margin-top: 5px;
}

header h2 {
  font-style: italic;
  font-size: 1.25em;
  margin-top:13px;

}

header h3 {
  font-family: 'Inconsolata', monospace;
  font-size: .9em;
  text-transform: uppercase;
  letter-spacing: .05em;
  margin-top:30px;
  color:#222;
  font-weight: 400;
    
}

header #search { 
    position: absolute;
    top: 0px;
    right: 0px;
    margin-right: 0px;
    /* border-radius: 3px; */
}

header .search-icon {
  position:absolute;
  right:0px;
  bottom:8px;
}
header .search {
    margin-right:0px;
    padding-right:20px;
    border: none;
    /* padding: 5px 10px; */
    color: #222;
    text-align: right;
    font-size: .9em;
    border-bottom: 2px solid #333;
    text-transform: uppercase;
    font-family: 'Inconsolata', monospace;
    font-size: .8em;
    letter-spacing:.1em;
}

header .search:focus {
  outline:none;
}


header .view {
  position:absolute;
  right:0px;
  bottom:0px;
}

.back {
  font-size:.9em;
  cursor:pointer;
  margin:0px;
  padding-top:30px;
  padding-bottom:10px;
}

.back div{
  float:left;
  position:relative;
  top: -1px;
  color: rgb(229, 255, 0);
}




.time-block{
    font-family: 'Inconsolata', monospace;
    font-size: .8em;
    text-transform: uppercase;
    letter-spacing: .05em;
    margin-top: 30px;
    color:#222222;
    display:flex;
    justify-content: space-between;
}

.time-block h2 { 
  padding: 10px 0px 10px 0;
  font-weight:500;
  
}

.time-block .room { font-weight:300; padding-left:20px; font-size:1em;}


h2.button {
  padding:10px 15px;
  border:1px solid black;
}

.time--name {
  text-transform: none;
  font-size:1.5rem;
  letter-spacing: 0;
  margin-bottom: 5px;
}

.time--name a {
  text-decoration:none;
}

.time--name a:hover {
  text-decoration:underline;
  text-decoration-color:rgb(229, 255, 0);
}

.time--time {
  margin-bottom:5px;
  color:#222222;
  font-family: 'Inconsolata', monospace;
  font-size: .8em;
  text-transform: uppercase;
  letter-spacing: .05em;
  margin-top:50px;
  color:#222;
}
.time a .button { 
  padding:10px 15px;
  border:1px solid black;
}

.time-block a:hover {
  background-color: rgb(229, 255, 0);
}
.work-image { 
  position:relative;
  height:0px;
  padding-bottom:150%;  
  background-color:#CCC;
  display:block;
}

.work-image img {
 max-width:100%;
 display:block;
 position:absolute;
 top:0px;
 left:0px;
}

#gallery {
  /*display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px,auto));
  grid-template-rows: repeat(6, auto);
  grid-gap: 1.5rem;*/
  display:flex;
  flex-wrap:wrap;
  box-sizing:border-box;
  margin:50px -10px 0 -10px;
}

#gallery a {
  margin-top: 20px;
  flex: 0 0 33%;
  box-sizing:border-box;
}

.piece{
  padding:0 10px;
}



#gallery a { text-decoration:none;}

#gallery .piece-image {
  position:relative;
  height:0px;
  padding-bottom:150%;
  margin-bottom:10px;
}


#gallery img {
 border:3px solid white;
 width:100%;
 display:block;
 position:absolute;
 top:0px;
 left:0px;
}

#gallery img:hover {
  cursor: pointer;
  border:3px solid rgb(229, 255, 0);
}


#gallery h1 {
  font-size: 1.25em;
  line-height:1.2em;
  margin-top: 5px;
}

#gallery h1:hover {
  text-decoration:underline;
  text-decoration-color:rgb(229, 255, 0);
}

#gallery h2 {
  font-family: 'Inconsolata', monospace;
    font-size: .9em;
    text-transform: uppercase;
    letter-spacing: .035em;
    margin-top: 5px;
    font-weight: 300;
    color: #444;
}

.work h1 {
  font-family: 'Tenor Sans', sans-serif;
  font-size: 3em;
  margin-left: -5px;
  line-height: 1em;
  margin-top: 10px;
  }

.work-image {
  max-width:100%;
  margin-top:30px;
}


.video {
  position:relative;
  width:100%;
  height:0px;
  padding-bottom:56.25%;
}

.video iframe{
  position:absolute;
  top:0px;
  left:0px;
  width:100%;
  height:100%;
}
 


footer {
  height:30px;
}

footer h3 {
  font-family: 'Inconsolata', monospace;
  font-size: .8em;
  text-transform: uppercase;
  letter-spacing: .05em;
  margin-top:30px;
  color:#222;
  padding-bottom:30px;
  padding-top:70px;
  font-weight:400;
}

footer img {
  width:100px;
  padding-right:50px;
}


.view-button {
    text-transform: uppercase;
    font-family: 'Inconsolata', monospace;
    font-size: .8em;
    padding: 0px;
    letter-spacing: .07em;
    font-weight: 300;
    color: #444;
    margin-left: 20px;
    border: none;
    padding-bottom: 5px;
    color: #999;

}

.view-button--active {
    border-bottom: 2px solid #222222;
    color: black;
    font-weight: 700;
}

.view-button:hover {
    color: black;
    font-weight: 700;
}

@media only screen and (max-width:800px) {
  header #search { 
    box-sizing: border-box;
    position: relative;  
   
    margin:40px 0 0 0;  
  }
  header h1 { font-size:3em;}
  header .search {
    box-sizing: border-box;
    width:100%; 
    padding-right:20px;
    font-size: 1em;
  }
  
  header .view { 
    position:relative;
    bottom:0px;
    right:0px;
    width:100%;
    text-align:right;
    margin-top:20px;
  }
  
    
  #gallery {
    margin-top:0px;
  }
}

@media only screen and (max-width:400px) {


  
  header .view button {
    background-color: white;
  }
  
  header h1 { 
  font-size:2.5em;
  }
  
  header h3, h2 {
    font-size:1em;
  }
  
}

@media only screen and (max-width:600px) {
  body {
    width:auto;
    padding:0 20px;
  }
  #gallery a {
    flex: 0 0 50%;
  }
}



@media only screen and (min-width:1140px) {
  
  #gallery a {
    flex: 0 0 25%;
  }
}

